<template>
    <div class="user-feedback" :style="style">
        <div class="com-title-bar">
            <div class="box">
                <div class="back" @click="$routerBack"></div>
                <div class="title">意见反馈</div>
                <div class="action">
                    <div class="button"></div>
                </div>
            </div>
        </div>
        <div class="option">
            <div class="box">
                <div class="item" :class="option.active === 0 ? 'active' : ''" @click="optionHandel(0,'无法播放')">无法播放</div>
                <div class="item" :class="option.active === 1 ? 'active' : ''" @click="optionHandel(1,'播放卡顿')">播放卡顿</div>
                <div class="item" :class="option.active === 2 ? 'active' : ''" @click="optionHandel(2,'搜索不准')">搜索不准</div>
                <div class="item" :class="option.active === 3 ? 'active' : ''" @click="optionHandel(3,'推荐不准')">推荐不准</div>
                <div class="item" :class="option.active === 4 ? 'active' : ''" @click="optionHandel(4,'无法下载')">无法下载</div>
                <div class="item" :class="option.active === 5 ? 'active' : ''" @click="optionHandel(5,'其他')">其他</div>
            </div>
        </div>
        <div class="content">
            <textarea placeholder="描述问题" v-model="content"></textarea>
        </div>
        <div class="x-button">
            <div class="submit" @click="submitHandel">提交</div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                style: {
                    height: '0px'
                },
                option: {
                    active: 0,
                    text: '无法播放'
                },
                content: ''
            }
        },
        mounted() {
            let height = window.innerHeight;
            this.style.height = height + 'px';
        },
        methods: {
            optionHandel(index, text) {
                this.option.active = index;
                this.option.text = text;
            },
            submitHandel() {
                if (this.content === '') {
                    this.$toast('请输入反馈内容');
                    return;
                }

                this.$toast.loading({
                    mask: false,
                    message: '提交中...',
                    duration: 0,
                    forbidClick: true
                });

                this.$axios.post('/api/app/user/feedback/create', {
                    option: this.option.text,
                    content: this.content
                })
                    .then(response => {
                        let code = response.data.code;
                        if (code === 200) {
                            this.$toast.clear();
                            this.$dialog.alert({
                                title: '提示',
                                message: '感谢您的反馈，我们会努力完善~'
                            }).then(() => {
                                this.$router.replace('/user/index');
                            })
                        }
                    })
            }
        }
    }
</script>

