    .user-feedback {
        background: #F5F5F5;
    }

    .user-feedback .option {
        padding: 90px 0 0 0;
    }

    .user-feedback .option .box {
        display: flex;
        padding: 30px 30px 0 30px;
        flex-wrap: wrap;
        justify-content: space-between;
