        border-radius: 40px;
        background: #FFFFFF;
        margin: 0 0 30px 0;
    }

    .user-feedback .option .box .item.active {
        background: #FFE903;
    }


    .user-feedback .content {
        background: #FFFFFF;
        font-size: 30px;
        padding: 30px;
    }

    .user-feedback .content textarea {
        padding: 0;
        margin: 0;
        border: 0;
        width: 100%;
